export const UNKNOWN_ERROR_CODE = 'UNKNOWN_ERROR';
export const SYSTEM_ERROR_MESSAGE = 'Oops, we encountered a problem';
export const BAD_USER_INPUT = 'BAD_USER_INPUT';

export const AuthenticationErrorType = 'AuthenticationError';
export const ConnectionErrorType = 'ConnectionError';
export const UnknownSystemErrorType = 'UnknownSystemError';
export const InvalidRequestErrorType = 'InvalidRequestError';
export const UnsupportedPhotoErrorType = 'UnsupportedPhotoError';

export class UnsupportedPhotoError extends Error {
  constructor(message) {
    super(message); // (1)
    this.type = UnsupportedPhotoErrorType;
    Object.setPrototypeOf(this, UnsupportedPhotoError.prototype);
  }
}
export class AuthenticationError extends Error {
  constructor(message) {
    super(message); // (1)
    this.type = AuthenticationErrorType;
    Object.setPrototypeOf(this, AuthenticationError.prototype);
  }
}
export class InvalidRequestError extends Error {
  constructor(message, errors) {
    super(message);
    this.type = InvalidRequestErrorType;
    if (errors) {
      errors = errors.map ? errors : [errors];
    }
    this.errors = errors;
    Object.setPrototypeOf(this, InvalidRequestError.prototype);
  }
}
export class ConnectionError extends Error {
  constructor(message) {
    super(message); // (1)
    this.type = ConnectionErrorType;
    Object.setPrototypeOf(this, ConnectionError.prototype);
  }
}
export class UnknownSystemError extends Error {
  constructor(message) {
    super(message); // (1)
    this.type = UnknownSystemErrorType;
    Object.setPrototypeOf(this, UnknownSystemError.prototype);
  }
}
